.App-container{
    background-color : #ecf0f1;
    min-height: calc(100vh - 30px);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s;
    &.dark-theme{
        background-color: #565555;
    }
    .theme-toggle{
        position: relative;
        background-color: #000;
        border-radius: 25px;
        input{
            visibility: hidden;
            position: absolute;
            &:checked{
                & + label{
                    .ball{
                        transform: translateX(100%);
                    }
                }
            }
        }
        label{
            display: flex;
            padding: 6px;
            cursor: pointer;
            svg{
                fill: #ffe123;
                &:not(:last-of-type){
                    margin-right: 5px;
                }
            }
            .ball{
                position: absolute;
                border-radius: 50%;
                width: 30px;
                height: 30px;
                left: 4px;
                top: 3px;
                background-color: #fff;
                transition: all 0.5s;
            }
        }
    }   
}