.App-container{
    .social-media-container{
        position: relative;
        .social-media-list{
            .share-btn{
                width: 50px;
                height: 50px;
                background-color: #3987e8;
                fill: #fff;
                left: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                position: absolute;
                transition: all 0.5s;
                cursor: pointer;
            }
            display: flex;
            justify-content: center;
            top: 0;
            // left: 0;
            // transform: translateX(-50%);
            // transform: translateX(-50%) translateX(25px);
            width: 50px;
            height: 50px;
            transition: all 0.5s;
            overflow: hidden;
            background-color: #fff;
            border-radius: 50px;
            // pointer-events: none;
            // opacity: 0;
            .social-media-item{
                min-width: 50px;
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                border-radius: 50%;
            }
            &.show{
                pointer-events: auto;
                width: 250px;
                opacity: 1;
                .share-btn{
                    opacity: 0;
                    pointer-events: none;
                }
            }
        }
    }    
}