body{
  margin:0;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 400;
}
*{
  box-sizing: border-box;
}
$theme_color: #ffe123;
$purple_color: #3e2f5b;
.App {
  padding-top: 30px;
  // padding-bottom: 30px;
  .App-header {
    background-color: $theme_color;
    box-shadow: 2px 4px 5px 0 rgba(0, 0, 0, 0.45);
    padding: 5px 15px;
    font-weight: 500;
    text-align: center;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    @media only screen and (max-width: 600px) {
      padding-right: 40px;
    }
  } 
  .App-menu-container{
    .menu-toggle{
      position: fixed;
      right: 10px;
      top: 2px;
      font-weight: 500;
      // padding: 5px 15px;
      z-index: 9999;
      cursor: pointer;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      width: 26px;
      height: 26px;
      @media only screen and (max-width: 600px) {
        top: 10px;
      }
      div {
        width: 26px;
        height: 4px;
        position: relative;
        // transform-origin: 1px center;
        transform-origin: 0px 2px;
        background: #000;
        border-radius: 10px;
        transition: all 0.3s linear 0s;
        &:first-child {
          transform: rotate(0deg);
        }
        &:nth-child(2) {
          opacity: 1;
          transform: translateX(0px);
        }
        &:nth-child(3) {
          transform: rotate(0deg);
        }
      }
      &.menu-open{
        div{
            &:first-child {
                transform: rotate(45deg);
            }
            &:nth-child(2) {
                opacity: 0;
                transform: translateX(20px);
            }
            &:nth-child(3) {
                transform: rotate(-45deg);
            }
        }
      }
    }
    .menu-container{
      background-color: #ffe123;
      position: fixed;
      padding-top: 40px;
      padding-bottom: 15px;
      left: 0;
      top: 0;
      opacity: 0;
      z-index: 99;
      width: 100%;
      height: 100%;
      pointer-events: none;
      transform: translateY(-10%);
      transition: all 0.5s;
      @media only screen and (max-width: 600px) {
        padding-top: 60px;     
      }
      &.show{
        opacity: 1;
        pointer-events: auto;
        transform: translateY(0);
      }
      .menu-nav-list{
        height: 100%;
        overflow: auto;
        list-style: none;
        margin: 0;
        .menu-item{
          .menu-item-link{
            font-weight: 500;
            color: #000;
            display: inline-block;
            margin-bottom: 10px;
          }
        }
      }
    }
  }
  .App-content {
    // background-color: #6fbadd;
    padding: 5px 15px;
  } 
  .App-container{
    padding: 30px 15px;
    @media only screen and (max-width: 600px) {
      padding: 30px 15px 60px;
    }
  }
  // $social_panel_width: 
  .App-footer {
    background-color: #ffe123;
    padding: 5px 15px;
    text-align: center;
    position: fixed;
    border-radius: 12px 12px 0 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    .hashtag{
      color: #FF3F00;
      font-weight: 500;
    }
  } 
  .footer-get-in-touch{
    position: fixed;
    right: 30px;
    bottom: 30px;
    border-radius: 30px;
    font-weight: 500;
    border: 1px solid $purple_color;
    display: flex;
    align-items: center;
    padding: 5px 15px;
    cursor: pointer;
    color: $purple_color;
    fill: $purple_color;
    transition: all 0.5s;
    &>svg{
      display: none;
    }
    &:hover{
      color: #fff;
      fill: #fff;
      background-color: $theme_color;
      border-color: $theme_color;
    }
    @media only screen and (max-width: 600px) {
      background-color: #fff;
      &>svg{
        display: block;
      }
      &>span{
        display: none;
      }
    }
  }
  .footer-social-panel{
    position: fixed;
    bottom: 85px;
    right: 30px;
    border: 5px solid $theme_color;
    border-radius: 10px;
    text-align: center;
    background-color: #fff;
    transform: translateX(200%);
    transition: all 0.5s;
    .footer-social-panel-header-container{
      display: flex;
      justify-content: space-between;
      .footer-social-panel-header{
        background-color: $theme_color;
        color: #fff;
        display: inline-block;
        padding: 5px 10px;
        border-radius: 0 0 10px 10px;
        a{
          color: #F34213;
          font-weight: 600;
        }
        .heart{
          color: red;
          display: inline-block;
          animation: pump 1s infinite;
        }
        @keyframes pump{
          0%{ transform: scale( .75 ); }
          20%{ transform: scale( 1 ); }
          40%{ transform: scale( .75 ); }
          60%{ transform: scale( 1 ); }
          80%{ transform: scale( .75 ); }
          100%{ transform: scale( .75 ); }
        }
      }
      .footer-social-panel-close{
        fill: $theme_color;
        margin-top: -3px;
        cursor: pointer;
      }
    }
    .footer-social-panel-sub-header{
      text-transform: uppercase;
      color: rgba(#000F08, 0.5);
      font-weight: 500;
      margin: 10px;
    }
    .footer-social-panel-link{
      display: flex;
      margin-bottom: 15px;
      .footer-social-panel-item{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 50px;
        width: 50px;
        border: 1px solid #ccc;
        border-radius: 50%;
        margin: 0 10px;
        transition: all 0.3s;
        &:hover{
          box-shadow: 0 0 10px $theme_color;
        }
      }
    }
    &.show{
      transform: translateX(0);
    }
  }
}

