.App-container{
    background-color : #ecf0f1;
    min-height: calc(100vh - 30px);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s;
    .password-generator-container{
        width: 400px;
        color: #fff;
        background-color: #086375;
        padding: 15px;
        border-radius: 5px;
        h4{
            text-align: center;
        }
        .password-field{
            background-color: #1dd3b0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 5px;
            border-radius: 5px;
            margin-bottom: 10px;
            &>svg{
                background-color: #086375;
            }
        }
        .form-setting{
            display: flex;
            justify-content: space-between;
            margin-bottom: 10px;
        }
    }
}