.App-container{
    .card-container{
        width: 270px;
        box-shadow: 0 0 10px #ccc;
        background-color: #fff;
        border-radius: 10px;
        margin: 15px auto;
        overflow: hidden;
        .card-img{
            height: 150px;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
        }
        .card-description{
            padding: 15px;
            .card-header{
                font-weight: 600;
                font-size: 1.2em;
                margin-bottom: 10px;
                height: 25px;
            }
            .card-description-text{
                height: 60px;
                margin-bottom: 15px;
                color: rgb(68, 68, 68);
            }
            .card-author{
                display: flex;
                .card-user-img {
                    min-width: 55px;
                    width: 55px;
                    height: 55px;
                    overflow: hidden;
                    border-radius: 50%;
                    background-size: cover;
                    background-position: center;
                    background-repeat: no-repeat;
                }
                .card-user-details{
                    padding-left: 10px;
                    width: 100%;
                    .card-username{
                        font-weight: 600;
                        height: 20px;
                        margin-bottom: 5px;
                    }
                    .card-date{
                        color: rgb(68, 68, 68);
                        height: 20px;
                    }
                }
            }
        }
    }
    .animated-bg {
        // position: relative;
        // &:before{
        //     content: "";
        //     position: absolute;
        //     width: 150px;
        //     left: -150px;
        //     height: 100%;
            background-image: linear-gradient(to right, #f6f7f8 0%, #edeef1 10%, #f6f7f8 20%, #f6f7f8 100%) !important;
            background-size: 200% 100% !important;
            animation: bgPos 1s linear infinite !important;
            background-repeat: repeat !important;
            // animation: bgPos 1s cubic-bezier(0.4, 0.0, 0.2, 1) infinite !important;
        // }
    }
    @keyframes bgPos {
        from {
            left: -300px;
        }
        to   {
            left: 100%;
        }
    }
    @keyframes bgPos {
        0% {
          background-position: 50% 0;
        }
        100% {
          background-position: -150% 0;
        }
      }
    
}